.row{
    margin-left: 20px;
    color: #fff;
}
.row h2{
    font-size: 1rem;
    padding: 0 20px;
}
.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}
.posters::-webkit-scrollbar{
    display: none;
}
.poster{
    max-height: 220px;
    margin-right: 10px;
    cursor: pointer;
}
.poster:hover{
    transform: scale(1.03);
}
.smallPoster {
    max-height: 110px;
    margin-right: 10px;
    cursor: pointer;
}
.smallPoster:hover{
    transform: scale(1.03);
}