.banner{
    background-size: cover;
    height: 90vh;
    color: rgb(255, 255, 255)
}
.content{
    height: 190px;
    padding-left: 15px;
}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-top: 41vh;
    padding-bottom: 1rem;    
}
.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding : 0.8rem 2.2rem;
    background-color: rgba(51, 51, 51, 0.5);
    cursor: pointer;
    margin-right: 1rem;
}
.button:hover{
    color: black;
    background-color: #e6e6e6;
}
.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.9rem;
    height: 1rem;
    max-width: 360px;
}
.fade_bottom{
    height: 65vh;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.6), #111);
}